import React, { Component } from 'react';

class ItemMainComponent extends Component {
  render() {
    return (
      <div id='item_main'>
        <section>
          <div className="container">
            <div className="gap">
              <div className="wrap">
                <div className="item_title_box">
                  <h2>  3P 2</h2>
                </div>
                <div className="item_img_info">
                  <ul>
                    <li>
                      <div className="item_img_box">
                        <img src="./img///list3/.png" alt="  3P 2" />
                      </div>
                      <div className="item_menu_box">
                        <ul>
                          <li><a href="#!"></a></li>
                          <li><a href="#!"></a></li>
                          <li><a href="#!"></a></li>
                        </ul>
                      </div>
                    </li>
                    <li>
                      <div className="item_option_box">
                        <div className="item_price">
                          <dl className="item_sale">
                              <dt></dt>
                              <dd><em className="item_ori_price">32,000</em></dd>
                              <dt></dt>
                              <dd><strong>26,900</strong></dd>
                          </dl>
                          <dl className="item_volume">
                              <dt>/</dt>
                              <dd>3(450ml, 900ml, 2L)</dd>
                          </dl>
                          <dl className="item_delivery">
                            <dt></dt>
                            <dd>3,000</dd>
                            <dt></dt>
                            <dd>EARTHMILE</dd>
                            <dt></dt>
                            <dd></dd>
                          </dl>
                        </div>
                        <div className="input_option">
                          <div className="input_option_chk">
                            <span className="chk_icon"></span>
                            <span className="chk"></span>
                            <span className="chk_option"><strong>26,900</strong></span>
                            <input type="number" min="1" max="10" value="1" />
                          </div>
                          <div className="input_option_total">
                            <dl className="total">
                                <dt>
                                    <strong> </strong>
                                </dt>
                                <dd>
                                    <strong>26,900</strong>
                                    <span></span>
                                </dd>
                            </dl>
                        </div>
                        </div>
                        <div className="item_btn">
                          <a href="#!" className="item_order"></a>
                          <a href="#!" className="item_cart"></a>
                        </div>
                      </div>
                    </li>
                  </ul>
                </div>
              </div>
            </div>
          </div>
        </section>
      </div>
    );
  }
}

export default ItemMainComponent;