import React, { Component } from 'react';
import {Outlet, Link} from 'react-router-dom';

class Section2Component extends Component {
  render() {
    return (
      <div id='section2'>
        <section>
          <div className="container">
            <div className="gap">
              <div className="wrap">
                <div className="ecostore">
                  <Link to='/StoreComponent'>
                    <img src="https://ParkHee-yeon.github.io/react_courageman/img//ico_ecostore.png" alt="" />
                    <div className="description-box">
                      <h4>     <br />    </h4>
                      <h3>  </h3>
                    </div>
                  </Link>
                </div>
                <div className="zero_news">
                  <div className="zerowaste">
                  <Link to='/ZerowasteComponent'>
                    <div className="description-box">
                      <h4>    </h4>
                      <h3></h3>
                    </div>
                    <img src="https://ParkHee-yeon.github.io/react_courageman/img//ico_zerowaste.png" alt="" />
                  </Link>
                  </div>
                  <div className="greennews">
                  <Link to='/GreenComponent'>
                    <div className="description-box">
                      <h4>    </h4>
                      <h3>   </h3>
                    </div>
                    <img src="https://ParkHee-yeon.github.io/react_courageman/img//ico_greennews.png" alt="  " />
                  </Link>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <Outlet/>
        </section>
      </div>
    );
  }
}

export default Section2Component;