import React, { Component } from 'react';
import DaumPostcode from 'react-daum-postcode';

class Postcode extends Component {
  
  //       
  onCompletePost = (data) => {
    this.props.onChangePostNum(data.zonecode);
    this.props.onChangeAddress1(data.address);
    // data.address       
    // <Postcode onChangeAddress1={this.onChangeAddress1}/> data.address 
  }

  render() {
    
    const postStyle = {
      position:'fixed',
      top:'50%',
      left:'50%',
      width:'400px',
      height:'500px',
      background:'#fff',
      zIndex:'2',
      border:'1px solid #ccc',
      marginTop:'-250px',
      marginLeft:'-200px'
    }

    return (
      <div>
        <DaumPostcode 
          style={postStyle}
          autoClose
          onComplete={this.onCompletePost}
        />
        {/* 
            autoClose    ,    
            onComplete   , data   
        */}
      </div>
    );
  }
}

export default Postcode;