import React, { Component } from 'react';
import {Outlet, Link} from 'react-router-dom';
import $ from 'jquery';

class HeaderComponent extends Component {
  constructor(props){
    super(props);
    this.state = {};
  }
  componentDidMount(){
    $('.main_menu').on({
      mouseenter(){
        $('.sub_menu').stop().fadeOut(0);
        $(this).next().stop().fadeIn(300);
        $('.sub_menu').addClass('menu_border');
      }
    });

    $('#nav').on({
      mouseleave(){
        $('.sub_menu').stop().fadeOut(300);
      }
    });

    $('.sub_menu').on({
      mouseleave(){
        $(this).stop().fadeOut(300);
      }
    });
  }
  render() {
    return (
      <div id='header'>
        <header>
          <div className="container">
            <div className="gap">
              <div className="wrap">
                <div id="sub_nav">
                  <ul>
                    <li><Link to='/LoginComponent'></Link></li>
                    <li><Link to='/JoinComponent'></Link></li>
                    <li><Link to=''></Link></li>
                  </ul>
                </div>
                <div id="main_nav">
                  <div id="logo">
                    <h1><Link to='/MainComponent'><img src="./img//logo.png" alt="logo" /></Link></h1>
                  </div>
                  <nav id="nav">
                    <ul>
                      <li>
                        <Link to='/StoreComponent' className="main_menu"></Link>
                        <div className="sub_menu">
                          <div className="sub_gap">
                            <ul>
                              <li>
                                <Link to='/StoreComponent'></Link>
                              </li>
                              <li>
                                <Link to='/StoreComponent'></Link>
                              </li>
                              <li>
                                <Link to='/StoreComponent'></Link>
                              </li>
                              <li>
                                <Link to='/StoreComponent'></Link>
                              </li>
                            </ul>
                          </div>
                        </div>
                      </li>
                      <li>
                        <Link to='/ZerowasteComponent' className="main_menu"></Link>
                        <div className="sub_menu">
                          <div className="sub_gap">
                            <ul>
                              <li><Link to='/ZerowasteComponent'> </Link></li>
                            </ul>
                          </div>
                        </div>
                      </li>
                      <li>
                        <Link to='/GreenComponent' className="main_menu">/</Link>
                        <div className="sub_menu">
                          <div className="sub_gap">
                            <ul>
                              <li>
                                <Link to='/GreenComponent'></Link>
                              </li>
                              <li>
                                <Link to='/GreenComponent'></Link>
                              </li>
                              <li>
                                <Link to='/GreenComponent'></Link>
                              </li>
                            </ul>
                          </div>
                        </div>
                      </li>
                      <li>
                        <Link to='' className="main_menu"></Link>
                        <div className="sub_menu">
                          <div className="sub_gap">
                            <ul>
                              <li>
                                <Link to=''></Link>
                              </li>
                              <li>
                                <Link to=''></Link>
                              </li>
                              <li>
                                <Link to=''></Link>
                              </li>
                            </ul>
                          </div>
                        </div>
                      </li>
                    </ul>
                  </nav>
                  <Outlet />
                </div>
              </div>
            </div>
          </div>      
        </header>
      </div>
    );
  }
}

export default HeaderComponent;