import React, { Component } from 'react';
import {Outlet, Link} from 'react-router-dom';

class BestItemComponent extends Component {
  render() {
    return (
      <div id='best_item'>
        <section>
          <div className="container">
            <div className="gap">
              <div className="wrap">
                <div className="best_title_box">
                  <h2> </h2>
                </div>
                <div className="best_item_box">
                  <ul>
                    <li>
                      <div className="best_img_box">
                        <Link to='/StoreInfoComponent'>
                          <img src="./img///list3/.png" alt="  3P 2" />
                        </Link>
                      </div>
                      <div className="best_info_box">
                        <strong className="name">
                          <Link to='/StoreInfoComponent'>
                            <span>
                                3P 2
                            </span>
                          </Link>
                        </strong>
                        <div className="line"></div>
                        <span>15,000</span>
                      </div>
                    </li>
                    <li>
                      <div className="best_img_box">
                        <Link to='/StoreInfoComponent'>
                          <img src="./img///list3/.png" alt="  ()" />
                        </Link>
                      </div>
                      <div className="best_info_box">
                        <strong className="name">
                          <Link to='/StoreInfoComponent'>
                            <span>
                                ()
                            </span>
                          </Link>
                        </strong>
                        <div className="line"></div>
                        <span>2,200</span>
                      </div>
                    </li>
                    <li>
                      <div className="best_img_box">
                        <Link to='/StoreInfoComponent'>
                          <img src="./img///list3/.png" alt="  (50)" />
                        </Link>
                      </div>
                      <div className="best_info_box">
                        <strong className="name">
                          <Link to='/StoreInfoComponent'>
                            <span>
                                (50)
                            </span>
                          </Link>
                        </strong>
                        <div className="line"></div>
                        <span>2,200</span>
                      </div>
                    </li>
                  </ul>
                  <Outlet/>
                </div>
              </div>
            </div>
          </div>
        </section>
      </div>
    );
  }
}

export default BestItemComponent;